  width: 340px;
  position: relative;
  top: 10%;
  margin: 0 0 0 60%;
  padding: 32px;
  background-color: #fff;
  box-shadow: 0px 18px 30px 0px #384ad6;
  border-radius: 4px;
}

.login-form .login-form-forgot {
  float: right;
}

.login-form .login-form-button {
  width: 100%;
}

.login-form-button{
  width: 100%;
  height: 47px;
  position: relative;
  background: linear-gradient(270deg, #ff4762 0%, #fe865b 100%);
  border: none !important;
  color: white;
  font-weight: bold;
  font-size: 16px;
  cursor: pointer;
}

.login-img{
  width: 50%;
}

.login-img img{
  width: 100%
}

.login-bgImg {
  width: 100%;
  background: url('../../assets/img/background1.png');
  background-size: cover;
  background-repeat: no-repeat;
  height: calc(100vh - 60px - 100px);
  background-position: center;
  /* overflow: hidden; */
}

.login-frame{
  height: 100%;
  background-color: rgb(9, 31, 56);
}

.login-logo{
  background-image: url('../../assets/img/logo1.png');
  background-repeat: no-repeat;
  background-position: center;
  width: 100px;
  height: 100px;
  position: absolute;
  left: 0px;
  right: 0px;
  margin: 0 auto;
}